.fa-bookmark{
    color: white;
}

.main-body{
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 0px 20px;
    padding-top: 30px;
}

.type-section{
    display: flex;
    gap: 25px;
}

.type-label{
    color: var(--main-gray);
    font-weight: bold;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    font-size: 25px;
    font-weight: 800;
}

.type-label:hover{
    color: white;
}


.view-point{
    background: none;
    background-color: rgb(9, 9, 9);
}

.categories-section{
    display: flex;
    gap: 25px;
}

.category-label{
    color: var(--main-gray);
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    border-bottom: 2px solid transparent;
    padding-bottom: 5px;
    font-weight: 800;
    font-size: 15px;
}

.category-label:hover{
    color: white;
    border-bottom: 2px solid green;
}


.search-section{
    display: grid;
    gap: 10px;
    grid-template-columns: 1fr auto;
}

.search-container{
    display: flex;
    background-color: var(--main-gray);
    align-items: center;
    gap: 7px;
    width: 1fr;
    padding: 5px;
    border-radius: 5px;

}

#search-field{
    width: 100%;
    height: 100%;
    background-color: transparent;
    border-color: transparent;
    color: white;
    border-inline: none;
}

#search-field:focus{
    outline: none;
}

.search-icon{
    color: white;
    transform: scale(0.6);
    cursor: pointer;
}

#filters{
    padding: 8px 10px;
    border-radius: 5px;
    color: white;
    background-color: var(--main-gray);
    font-size: 10px;
    cursor: pointer;
}

.songs-list{
    display: flex;
    flex-direction: column;
    gap: 10px;
    overflow: scroll;
    max-height: 465px;
}

.list-item{
    height: 70px;
    background-color: violet;
    min-height: 70px;
}